
import { RoundService } from "@/services/RoundService";
import { DISABLE_FULL_SCREEN } from "@/store/modules/UIModule";
import { Tick } from "@/model/Tick";
import { Options, Vue } from "vue-class-component";

@Options({
  data() {
    return {
      explosion: false,
      roundService: new RoundService(), //TODO - Ne viene generato uno nuovo a ogni round?
    };
  },
  computed: {
    bombValue() {
      return this.$store.state.round.bombTime;
    },
  },
  mounted() {
    const audio = new Audio("Explosion.mp3");
    const tick = new Tick();
    tick.start();
    //TODO - Spostare fuori??
    setTimeout(async () => {
      tick.stop();
      this.explode();

      const audioDuration = audio.duration;
      audio.play();

      if ("vibrate" in navigator) {
        (navigator as Navigator).vibrate(900);
      }

      setTimeout(() => {
        this.roundService.onRoundEnd();
        this.$store.commit(DISABLE_FULL_SCREEN);
      }, audioDuration * 1000); // Tempo animazione esplosione
    }, this.$store.state.round.bombTime * 1000);
  },
  methods: {
    explode() {
      this.explosion = true;

      console.log(this.$refs);
      const numDivs = 20;
      const colors = ["#F9B232", "#ff9800", "#ffcb3b", "#ffc107", "#ff5722"];
      const explosionContainer = this.$refs.explosionContainerEl;
      const explosion = this.$refs.explosionEl;
      const colorDelay = 200;

      for (let i = 0; i < numDivs; i++) {
        const duplicate = explosion.cloneNode();
        duplicate.style.background = colors[(i + 1) % colors.length];
        duplicate.style.animationDelay = `${(i + 1) * colorDelay}ms`;
        explosionContainer.appendChild(duplicate);
      }
    },
  },
})
export default class Bomb extends Vue {}
